import React from 'react';
import {  Row, message, Col ,Icon} from 'antd';
import {  Input, Button } from 'antd';
import {Link} from 'react-router-dom';
import './Contact.css'
import GoogleMapReact from 'google-map-react';
import axios from 'axios';
import {GoogleApiWrapper} from 'google-maps-react';
import Maps from './Maps.js';
 
const AnyReactComponent = ({ text }) =>( <div style={{color:"white",fontSize:"15px"}}> 
    <Icon size={50}type="copyright" theme="twoTone" />{text}
    </div>);
const { TextArea } = Input;

const error = (msg)=>{
    message.error(msg)
}

const success =(msg)=>{
    message.success(msg);
}

class Contact extends React.Component {

    state = {
        firstName : '',
        lastName : '',
        message:'',
        email:'',
    }

	static defaultProps = {
        center: {
          lat: 52.2105108,
          lng: 0.1629575
       
        },
        zoom: 11
      };
changeData = (e)=>{
    this.setState({
        [e.target.name] : e.target.value
    })
}

clearData =()=>{
    this.setState({
        firstName : '',
        lastName : '',
        message:'',
        email:'',
    })
}

sendEmail = ()=>{
    if(!this.state.firstName){
        error("Please enter First Name")
    }else if(!this.state.lastName ){
        error("Please enter Last Name")
    }else if(!this.state.message ){
        error("Please enter message")
    }else if(!this.state.email ){
        error("Please enter emailid")
    }else if (this.state.email){
       if( !this.state.email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i) ){
           error("Enter valid Emailid")
       }else{
          let data = {
               firstName:this.state.firstName,
               lastName:this.state.lastName,
               message:this.state.message,
               email:this.state.email,

           }
        axios.post('/api/email/', data)
          .then(function (response) {
            success(response.data.message);
            this.clearData();
          }.bind(this))
          .catch(function (err) {
            console.log(err)
            error("Error");
          });
       }
    }

}

render (){

	return(
			<div>	
                <div className="head">
                    <Row>
                        <Col>
                          <p> Contact</p>
                        </Col>
                    
                    </Row>   
                </div>
                <div>   
                    <Row>
                        <Col>
                           <div>
                           <div>
                           <div style={{ height: '50vh', width: '100%' }}>
                           <GoogleMapReact
                             bootstrapURLKeys={{ key: "AIzaSyBkb3x01V7GlDoQuROaeWiwip7kS_93nzA" }}
                             defaultCenter={this.props.center}
                             defaultZoom={this.props.zoom}
                           >
                           <AnyReactComponent
                               lat={52.2105152}
                               lng={0.162523}
                               text="Cambridge Care Services"
                               
                             />
                         
                           </GoogleMapReact>
                         
                         </div>
                           </div>
                           </div>
                            

                        </Col>
                    </Row>
                    
                </div>
                <div>
                    <Row>
                    <a style={{color:'black'}} href='https://www.google.com/maps/place/71+Barnwell+Rd,+Cambridge+CB5+8RG,+UK/@52.2105423,0.1608815,17z/data=!3m1!4b1!4m8!1m2!2m1!1sCambridge+Care+Services++71,+Barnwell+Road++Cambridge+-+CB58RG!3m4!1s0x47d8706a24074e67:0x351d6c5b71d9685!8m2!3d52.210539!4d0.1630702'>Get Directions</a>
                        <Col md={12} lg={12}>
                            <div className="form-box">
                                <h1>Contact Form</h1>
                                <Input name="firstName" value={this.state.firstName} onChange = {this.changeData} placeholder="First name" />
                                <Input name="lastName" value={this.state.lastName} onChange = {this.changeData} placeholder="Last name" />
                                <TextArea name="message" value={this.state.message} onChange = {this.changeData} rows={4} placeholder="Message" />
                                <Input name="email" value={this.state.email}  onChange = {this.changeData} type="email" placeholder = "Email"/>
                                <Button onClick={this.sendEmail}>Submit</Button>
                            </div>
                               
                        </Col>

                        <Col md={12} lg={12}>
                            <div className="form-box">
                                <h1>Our Address</h1>
                                <p> <Icon type="dribbble-square" />Cambridge Care Services</p>  
                                <p>71, Barnwell Road</p>
                                <p> Cambridge - CB58RG</p>
                                <p> <Icon type="mail" />info@cambridgecareservices.co.uk</p>
                                <p> <Icon type="phone" /> +44 7917 611 425</p>
                                <p> <Icon type="ie-circle" theme="filled" /> http://www.cambridgecareservices.co.uk/</p>
                            </div>

                        </Col>

                    </Row>
                </div>
                <div>
                
                </div>
           
			</div>
		)
	}
}
export default Contact;







